import React from 'react';
import './clickHappy.css';

const ClickHappy = ({ clickIncome, clickCashOut }) => {


    return (
        <div className='bg-navy pb2'>
            <h1 className='mb0'>Happy Clicker</h1>
            <br></br>
            <label className='yellow'>Click for Happy!</label>
            <br></br>
            <button id='buckaclick' className='pa1 f2 grow bw2 shadow-5'  onClick={clickCashOut}>+{clickIncome} happy</button>
        </div>
    );
}

export default ClickHappy;