import React, { Component } from 'react';
import CloseMainMenu from './CloseMainMenu';
import Reset from './Reset';
import Scroll from '../components/Scroll.js';

// Import Css
import './mainMenu.css';

class MainMenu extends Component {
    constructor(){
        super()       
        this.state = {
            isOpen: true,
        }
}

openMenu=()=>{
    this.setState({
        isOpen: true
    });
}

closeMainMenu=()=>{
    console.log('close Main Menu');
    this.setState((state) => ({ isOpen: false}));
}

render()   {



    // destructure
    const { isOpen } = this.state;
    const { closeMainMenu } = this;
    
    if (isOpen === true){
        let localUpgradesLoad = localStorage.getItem("localUpgradesV1");
        let activeUpgrades = JSON.parse(localUpgradesLoad);
        return(
        <div id="mainMenu">
            <h1 >Main Menu</h1>
            <h2>Welcome to Idle Trillionaire</h2>
            <Scroll>

                <h3>Game Rules v1.02</h3>
                <p>- reach a trillion dollars or beyond</p>
                <p>- earn $ and happy to buy cards that increase the rate you earn $ and happy</p>
                <p>- watch out you don't get -10,000 happy...</p>
                <p>- you lose if your balance reaches -$100,000.</p>
                <p>I hope this app can offer some perspective into how big a number 1 trillion really is</p>

                <h3>Your Card Buying History</h3>
                    {
                        activeUpgrades.map((name, i) => {
                            return(
                            <p>{i+1} - {activeUpgrades[i].name}</p>
                            );
                        })
                    }

                <h3>About</h3>
                <p>by The Slanted Room</p>
                
                <br></br>
                <p>This app is currently in active development</p>
                <p>there might be bugs</p>
                <p>Resetting progress (red button at page bottom) will reset your save if you want to start over.</p>
                <p>Thanks for checking it out</p>
                <br></br>

                <h3>Credit</h3>
                <p>"Cats Images by David Revoy by way of Robohash"</p>
                <h3>Links</h3>
                <a href="https://theslantedroom.github.io/Last-Man-Standing-Mini-Game/" target="">Last Man Standing - Mini Game</a>
                <br></br>
                <a href="https://theslantedroom.github.io/steve.yee/" target="_blank" rel="noreferrer">The Slanted Room</a>
                <br></br>
                <a href="https://www.patreon.com/user?u=49300800" target="_blank" rel="noreferrer">Support the Developer</a>
                <br></br>
                <a href="https://www.paypal.com/donate?hosted_button_id=NN7GRPQRZU62S">Donate</a>
               
                
            </Scroll>
            <CloseMainMenu id="mainMenuClose" closeMainMenu = {closeMainMenu}/>
            <Reset resetButton = {this.props.resetButton}/>
        </div>  
        );   
    } else {
        return(
            <div>

            </div>     
            ); 
        }
    }
}


export default MainMenu; 

