import React, { Component } from 'react';
import MainMenu from '../components/MainMenu.js';
import CardList from '../components/CardList.js';
import TypeCash from '../components/TypeCash.js';
import ClickHappy from '../components/ClickHappy.js';
import StatsBox from '../components/StatsBox.js';
// import UpgradeBox from '../components/UpgradeBox.js';
import MainCounter from '../components/MainCounter.js';
import Scroll from '../components/Scroll.js';
import { upgrades } from './upgrades.js';
import './App.css';
// import SearchBox from '../components/SearchBox.js';



class App extends Component {
    constructor(){
        super()       
        this.closeMainMenuRef = React.createRef();
        this.state = {
            timeSpeed: 300,
            upgrades: upgrades,
            searchfield: '',
            mainCounter: 0,
            mainHappiness: 0,
            hoursPassed: 0,
            daysPassed: 0,
            yearsPassed: 0,
            typeIncome: 0,
            dollarTypeInvoice: 0,
            dollarIncome: 0,
            clickIncome: 0,
            happinessIncome: 0,
            spending: 0,
            annualBonus: 0,
        }


        // Retrieving localSaveFile:
        if (localStorage.getItem("localSaveFile") != null) 
        {
            let localSaveFile, activeSaveFile;
            localSaveFile = localStorage.getItem("localSaveFile");
            activeSaveFile = JSON.parse(localSaveFile);

            // load save into this.state 
            if (activeSaveFile != null){
                console.log('save file detected');
                this.state.mainCounter = activeSaveFile.mainCounter;
                this.state.mainHappiness = activeSaveFile.mainHappiness;
                this.state.hoursPassed = activeSaveFile.hoursPassed;
                this.state.daysPassed = activeSaveFile.daysPassed;
                this.state.yearsPassed = activeSaveFile.yearsPassed;
                this.state.typeIncome = activeSaveFile.typeIncome;
                this.state.dollarTypeInvoice = activeSaveFile.dollarTypeInvoice;
                this.state.dollarIncome = activeSaveFile.dollarIncome;
                this.state.clickIncome = activeSaveFile.clickIncome;   
                this.state.happinessIncome = activeSaveFile.happinessIncome;
                this.state.spending = activeSaveFile.spending; 
                this.state.annualBonus = activeSaveFile.annualBonus;
            } 
        } else //create local savefile
        {
            console.log('no save file detected, new file created');
            this.saveGame();
        };   
        
        // Retrieving localUpgradesV1:
        if (localStorage.getItem("localUpgradesV1") != null) 
        {
            let localUpgradesLoad = localStorage.getItem("localUpgradesV1");
            let activeUpgrades = JSON.parse(localUpgradesLoad);
            if (activeUpgrades != null)
            {
                //update this.state.upgrades to reflect any owned cards listed in activeUpgrades
                // console.log('found upgrade file', activeUpgrades);

                //get index of this.state.upgrade for any cards with matching name in activeUpgrades
                activeUpgrades.forEach((cardElement, index) => {
                // console.log('cardfound', activeUpgrades[index].name);
                let OwnedIndex = this.state.upgrades.findIndex(card => card.name === activeUpgrades[index].name);    
                // console.log('foundindex owned ', OwnedIndex);
                //update this.state.upgrade to reflect owned cards
                if ( this.state.upgrades[OwnedIndex] !== undefined)
                {
                    this.state.upgrades[OwnedIndex].owned = true;
                }
                
                });            
            } 
        } else //create local localUpgradesV1 savefile
        {
            console.log('no save file detected, new localUpgradesV1 created');
            const emptyUpgradeSave = [];
            let localUpgradesJSON = JSON.stringify(emptyUpgradeSave);
            localStorage.setItem("localUpgradesV1", localUpgradesJSON);
        }; 
    }



    clickCard = (key, upgradeCost, upgradeDollarIncome, upgradeClickIncome, upgradetypeIncome, upgradeHappiness, 
        upgradeSpending, UpgradeAnnualBonus, upgradeHappinessCost, upgradeHappinessBoost, upgradeDollarBoost, upgradeID) =>
        {

        // report owned status
        if (this.state.upgrades[key].owned === true){
            // console.log('already own ');
            return
        };

        // Applys Upgrade / check if can afford
        if ((this.state.mainCounter >= upgradeCost && this.state.mainHappiness >= upgradeHappinessCost && this.state.upgrades[key].owned === false) || 
            (upgradeCost === 0 && this.state.mainHappiness >= upgradeHappinessCost && this.state.upgrades[key].owned === false) || 
            ( upgradeHappinessCost === 0 &&  this.state.mainCounter >= upgradeCost && this.state.upgrades[key].owned === false )
            ){

            // buy item
            this.setState((state) => ({ dollarIncome: state.dollarIncome + upgradeDollarIncome}));
            this.setState((state) => ({ clickIncome: state.clickIncome + upgradeClickIncome}));
            this.setState((state) => ({ mainCounter: state.mainCounter - upgradeCost}));
            this.setState((state) => ({ typeIncome: state.typeIncome + upgradetypeIncome}));
            this.setState((state) => ({ happinessIncome: state.happinessIncome + upgradeHappiness}));
            this.setState((state) => ({ spending: state.spending + upgradeSpending}));
            this.setState((state) => ({ annualBonus: state.annualBonus + UpgradeAnnualBonus}));
            this.setState((state) => ({ mainHappiness: state.mainHappiness - upgradeHappinessCost}));
            this.setState((state) => ({ mainHappiness: state.mainHappiness + upgradeHappinessBoost}));
            this.setState((state) => ({ mainCounter: state.mainCounter + upgradeDollarBoost}));

            if(this.state.spending < 0){
                this.setState((state) => ({ spending: 0}));
            }
            if(this.state.dollarIncome < 0){
                this.setState((state) => ({ dollarIncome: 0}));
            }


            // Gets the index of the clicked card with matching name
            let index = this.state.upgrades.findIndex(card => card.name === this.state.upgrades[key].name);
            // console.log('matchingNameindex',index);
            // console.log('this.state.upgrades.name', this.state.upgrades[key].name);

            // set bought item to owned
            const buyUpgrades = this.state.upgrades;
            buyUpgrades[index].owned = true;

            this.setState((state) => ({ upgrades: buyUpgrades}));
            // console.log('purchased ');
            let cardName = this.state.upgrades[key].name;
            this.addCardsLocalUpgrades(cardName)  

        } else if (this.state.mainCounter <= upgradeCost) {
            // console.log('can NOT afford ');
        }   
        
    }

    mapEventListeners = () => {
        setTimeout(() => {
        // console.log('mapeventlisteners');
        // Loop thru upgrade list and map Eventlisteners
        for (const [key] of Object.entries(this.state.upgrades)) {
            
            let upgradeCost = this.state.upgrades[key].cost;
            let upgradeDollarIncome = this.state.upgrades[key].dollarIncome;
            let upgradeClickIncome = this.state.upgrades[key].clickIncome;
            let upgradeTypeIncome = this.state.upgrades[key].typeIncome;
            let upgradeHappiness = this.state.upgrades[key].happinessIncome;
            let upgradeSpending = this.state.upgrades[key].spending;
            let UpgradeAnnualBonus = this.state.upgrades[key].annualBonus;
            let upgradeHappinessCost = this.state.upgrades[key].happinessCost;
            let upgradeHappinessBoost = this.state.upgrades[key].happinessBoost;
            let upgradeDollarBoost = this.state.upgrades[key].dollarBoost;
            let upgradeID = this.state.upgrades[key].name;
            // add click listener to each card 
            let clickCard = this.clickCard;
            document.getElementById(upgradeID).addEventListener("click", function(arg1, arg2, arg3, arg4, arg5, arg6, arg7, arg8, arg9, arg10, arg11, arg12, arg13){
                clickCard(key, upgradeCost, upgradeDollarIncome, upgradeClickIncome, upgradeTypeIncome, upgradeHappiness, upgradeSpending, UpgradeAnnualBonus, upgradeHappinessCost, upgradeHappinessBoost, upgradeDollarBoost, upgradeID); 
            }, false);
        }
      }, 10);  
    }


    // Component did mount with main ticker
    componentDidMount() {  
        // console.log('componentDidMount');
        this.mapEventListeners();

        // Main Ticker
        const mainticker = setInterval(() => {
            const { hoursPassed, daysPassed } = this.state;
            if (hoursPassed < 24){
                this.setState((state) => ({ hoursPassed: state.hoursPassed + 1}));
            }
            // Add a day
            else {
                
                this.setState((state) => ({ daysPassed: state.daysPassed + 1}));
                this.setState((state) => ({ hoursPassed: 0}));
                this.setState((state) => ({ mainCounter: state.mainCounter - state.spending}));
                this.setState((state) => ({ mainHappiness: state.mainHappiness + state.happinessIncome}));
                // Add a year
                if (daysPassed === 365){
                    alert(`Conratulations on completing year ${this.state.yearsPassed+1}.  Your annual bonus ( $${this.state.annualBonus.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")} ) has been deposited.`);
                    this.setState((state) => ({ daysPassed: 0}));
                    this.setState((state) => ({ yearsPassed: state.yearsPassed + 1}));
                    this.setState((state) => ({ mainCounter: state.mainCounter + state.annualBonus}));
                }
            }  
            // pass hour calculation
            this.setState((state) => ({ mainCounter: state.mainCounter + state.dollarIncome}));
            this.saveGame();

            // check win condition
            if (this.state.mainHappiness <= -10000){
                clearInterval(mainticker);
                alert('Oh no.. -1000 HAPPINESS... SUICIDE. you lose, Reset button at bottom to start again');
            }
            if (this.state.mainCounter <= -100000){
                clearInterval(mainticker);
                alert('Oh no..your in debt for -$100,000... SUICIDE. you lose, Reset button at bottom to start again');

            }
        }, this.state.timeSpeed); //end of main ticker
    }   //<<<end of componentDidMount

    // componentDidUpdate(){console.log('componentdidupdate');}

    componentWillUnmount() {
         // wipe Event listeners
        let old_element = document.getElementById("cardlist");
        let new_element = old_element.cloneNode(true);
        old_element.parentNode.replaceChild(new_element, old_element);
        clearInterval(this.componentDidMount.mainticker);
        // console.log('componentWillUnmount');
    }

    // add $ for each char typed
    onTypeChange = (event) => {
        this.setState((state) => ({ dollarTypeInvoice: state.dollarTypeInvoice + state.typeIncome }));
    }
    // Filter
    onSearchChange = (event) => {
        this.setState({searchfield: event.target.value})
    }

        // return number with commas
    numberWithCommas(inputMoney) {
        return inputMoney.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    }

    // process Card Effects
    processCardEffect(clickIncome, dollarIncome, typeIncome, dollarBoost, happinessIncome, happinessBoost, spending, annualBonus) {
        
        let e1 = clickIncome;
        if (clickIncome < 0){
            e1 = clickIncome ? '-$'+(clickIncome*-1).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")+' per Click for Happy. ' : '';
        } else {
            e1 = clickIncome ? '+$'+(clickIncome.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ","))+' per Click for Happy. ' : '';
        }

        let e2 = dollarIncome;
        if (dollarIncome < 0){
            e2 = dollarIncome ? '-$'+(dollarIncome*-1).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")+'/hour. ' : '';
        } else {
            e2 = dollarIncome ? '+$'+(dollarIncome.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ","))+'/hour. ' : '';
        }

        let e3 = annualBonus;
        if (typeIncome < 0){
            e3 = typeIncome ? '-$'+(typeIncome*-1).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")+' per character typed. ' : '';
        } else {
            e3 = typeIncome ? '+$'+(typeIncome.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ","))+' per character typed. ' : '';
        }

        let e4 = dollarBoost;
        if (dollarBoost < 0){
            e4 = dollarBoost ? '-$'+(dollarBoost*-1).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")+'. ' : '';
        } else {
            e4 = dollarBoost ? '+$'+(dollarBoost.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ","))+'. ' : '';
        }

        let e5 = happinessIncome;
        if (happinessIncome < 0){
            e5 = happinessIncome ? '-'+(happinessIncome*-1).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")+' happy/day. ' : '';
        } else {
            e5 = happinessIncome ? '+'+(happinessIncome.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ","))+' happy/day. ' : '';
        }

        let e6 = happinessBoost;
        if (happinessBoost < 0){
            e6 = happinessBoost ? '-'+(happinessBoost*-1).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")+' happy. ' : '';
        } else {
            e6 = happinessBoost ? '+'+(happinessBoost.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ","))+' happy. ' : '';
        }

        let e7 = spending;
        if (spending < 0){
            e7 = spending ? '-$'+(spending*-1).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")+' cost of living. ' : '';
        } else {
            e7 = spending ? '+$'+(spending.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ","))+' cost of living. ' : '';
        }
        let e8 = annualBonus;
        if (annualBonus < 0){
            e8 = annualBonus ? '-$'+(annualBonus*-1).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")+' annualy. ' : '';
        } else {
            e8 = annualBonus ? '+$'+(annualBonus.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ","))+' annualy. ' : '';
        }
       

        return e1+e2+e3+e4+e5+e6+e7+e8;
    }
    // SAVE to local storage
    saveGame = () => { 
        // Storing data:
        // console.log('saving data to local storage');
        let localSaveObj;
        localSaveObj = {
            hoursPassed: this.state.hoursPassed,
            daysPassed: this.state.daysPassed,
            yearsPassed: this.state.yearsPassed,
            mainCounter: this.state.mainCounter,
            mainHappiness: this.state.mainHappiness,
            typeIncome: this.state.typeIncome,
            dollarTypeInvoice: this.state.dollarTypeInvoice,
            dollarIncome: this.state.dollarIncome,
            clickIncome: this.state.clickIncome,
            happinessIncome: this.state.happinessIncome,
            spending: this.state.spending,
            annualBonus: this.state.annualBonus,
        };
        let localSaveObjJSON = JSON.stringify(localSaveObj);
        localStorage.setItem("localSaveFile", localSaveObjJSON);
    }

    
    addCardsLocalUpgrades = (cardName) => {
        let localUpgradesLoad = localStorage.getItem("localUpgradesV1");
        let activeUpgrades = JSON.parse(localUpgradesLoad);
        // Gets the index of the clicked card with matching name
        let thisCardIndex = this.state.upgrades.findIndex(card => card.name === cardName);
        //add this card cardName, to localUpgrade[]
        activeUpgrades.push(this.state.upgrades[thisCardIndex]);
        // console.log('found local Upgrade');
        let localUpgradesJSON = JSON.stringify(activeUpgrades);
        localStorage.setItem("localUpgradesV1", localUpgradesJSON);
    }

    // PAY into Total
    payout = (incomeSource) => {
        this.setState((state) => ({ mainCounter: state.mainCounter + incomeSource}));
    }
    // payout textbox cash
    cashOut = () =>{
        this.payout(this.state.dollarTypeInvoice);
        this.setState((state) => ({ dollarTypeInvoice: 0}));
        document.getElementById("inputText").value = "Type for money";
    }
    // payout click cash
    clickCashOut = () => {
        // this.payout(this.state.clickIncome);
        this.setState((state) => ({ mainHappiness: state.mainHappiness + this.state.clickIncome}));
    }
    // Reset game
    resetButton = () => {
        // alert('Sorry.. I have not programmed the confirm prompt yet, I hope you meant to click that!')
        clearInterval(this.mainticker);
        console.log('save file deleted');
        this.setState((state) => ({ mainCounter: 0}));
        this.setState((state) => ({ mainHappiness: 0}));
        this.setState((state) => ({ hoursPassed: 0}));
        this.setState((state) => ({ daysPassed: 0}));
        this.setState((state) => ({ yearsPassed: 0}));
        this.setState((state) => ({ typeIncome: 0}));
        this.setState((state) => ({ dollarTypeInvoice: 0}));
        this.setState((state) => ({ dollarIncome: 0}));
        this.setState((state) => ({ clickIncome: 0}));
        this.setState((state) => ({ happinessIncome: 0}));
        this.setState((state) => ({ spending: 0}));
        this.setState((state) => ({ annualBonus: 0}));
        
        // reset upgrades
        for (const [key] of Object.entries(this.state.upgrades)) {
            const noUpgrades = this.state.upgrades;
            noUpgrades[key].owned = false;         
            this.setState((state) => ({ upgrades: noUpgrades}));     
        }
        localStorage.removeItem("localUpgradesV1");
        localStorage.removeItem("localSaveFile");

        console.log('RESET EVENT no save file detected, new file created');
        const emptyUpgradeSave = [];
        let localUpgradesJSON = JSON.stringify(emptyUpgradeSave);
        localStorage.setItem("localUpgradesV1", localUpgradesJSON);

        console.log('save data reset');
        // setTimeout(() => {
        //     window.location.reload();
        // }, 10);
        this.closeMainMenuRef.current.closeMainMenu();
   
    }

    openMainMenu=()=>{
        this.closeMainMenuRef.current.openMenu();
        console.log('open Main Menu');
    }

    render()   {
        // can destructure this.functions later
        const { upgrades, searchfield, mainCounter, mainHappiness, dollarIncome, clickIncome, happinessIncome, spending, hoursPassed, daysPassed, yearsPassed, dollarTypeInvoice,typeIncome, annualBonus } = this.state;
        const filteredUpgrades = upgrades.filter(upgradeData => {
            return upgradeData.name.toLowerCase().includes(searchfield.toLowerCase());
        })
        return(
                <div className='tc'>
                    <MainMenu 
                    closeMainMenu= {this.closeMainMenu}
                    resetButton = {this.resetButton}
                    ref = {this.closeMainMenuRef}
                    />
                    
                    <h1 className='ma1'>Idle Trillionaire!</h1>
                    <MainCounter mainCounter = {mainCounter} numberWithCommas = {this.numberWithCommas} />
                    <StatsBox 
                    dollarIncome = {dollarIncome} 
                    hoursPassed = {hoursPassed} 
                    daysPassed = {daysPassed} 
                    yearsPassed = {yearsPassed} 
                    mainHappiness = {mainHappiness}
                    happinessIncome = {happinessIncome}
                    spending = {spending}
                    annualBonus = {annualBonus}
                    numberWithCommas = {this.numberWithCommas}
                    />

                    <Scroll>
                    <TypeCash 
                    onTypeChange = {this.onTypeChange}
                    dollarTypeInvoice ={dollarTypeInvoice}
                    typeIncome ={typeIncome}
                    cashOut = {this.cashOut}
                    />
                    <ClickHappy
                    clickIncome = {clickIncome}
                    clickCashOut = {this.clickCashOut}
                    />
                    {/* <SearchBox searchChange = {this.onSearchChange} /> */}
                    <CardList 
                    upgrades={filteredUpgrades} 
                    numberWithCommas = {this.numberWithCommas}
                    processCardEffect = {this.processCardEffect}
                     />
                    </Scroll>
                    <button className='openMenu'  onClick={this.openMainMenu}>menu</button>  
                </div>     
            ); 
        }
    }





export default App; 

